<template>
  <div class="nine-prize" :style="getPrizeStyle">
    <!-- 盒子 -->
    <div class="nine-prize-box">
      <div class="nine-prize-box__item" v-for="(item, index) in ds" :key="index"
        :class="getPositionClass(index, item.isSelect)" :style="getPosition(index, item.isSelect)">
        <div :style="{ ...childCommonStyle, backgroundColor: item.bgColor, borderRadius: item.isSelect ? '0' : '5px' }"
          class="item-child">
          <!-- img -->
          <div class="item-child-img">
            <img :src="item.img" />
          </div>
          <!-- text -->
          <div style="color:#333" class="item-child-text">
            <span>{{ item.text }}</span>
          </div>
          <!-- 编辑 -->
          <div class="edit-box">
            <div @click="editDsList(0, item, index)">编辑</div>
            <div @click="editDsList(1, item, index)">添加</div>
            <div @click="editDsList(2, item, index)">删除</div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="nine-prize-pointer" :style="whichPointerWH">
      <div contenteditable="true" ref="centeTipsTitle" class="title"
        @blur="handleBlur('centeTipsTitle', 'centeTips', 'title')" v-html="centeTips.title"></div>
      <div contenteditable="true" class="text" ref="centeTipsText"
        @blur="handleBlur('centeTipsText', 'centeTips', 'text')" v-html="centeTips.text">
      </div>
      <div contenteditable="true" class="remark" ref="centeTipsRemark"
        @blur="handleBlur('centeTipsRemark', 'centeTips', 'remark')" v-html="centeTips.remark"> </div>
      <!-- <div class="nine-prize-pointer__btn" :style="{ backgroundColor: pointerBackground }">
        <div :style="{ color: pointerTextColor }" v-debounce="onRun" v-show="!isStart">
          <span>{{ pointerText }}</span>
        </div>
        <van-loading :color="prizeBackground" v-show="isStart" size="36px" />
      </div> -->
    </div>
    <!-- <div class="dialog-mask"></div>
    <div class="dialog-box">
      <h5>恭喜中奖</h5>
      <div>
        <img src="" alt="">
      </div>
    </div> -->
    <!-- 弹窗背景层 -->
    <transition name="fade">
      <div v-if="visible || editVisible" class="overlay">
        <!-- 弹窗内容带动画 -->
        <transition name="popup">
          <div class="popup" v-if="visible">
            <div class="close-btn" @click="hidePopup('musicZj')">&times;</div>
            <h2>恭喜欧中！</h2>
            <img :src="ds[curSelect].img" alt="示例图片" />
            <p>{{ ds[curSelect].text }}</p>
          </div>
          <div class="form-container popup" style="width: 30vw">
            <h2>{{ editType[actveEditType] }}</h2>
            <form>
              <!-- 图片上传 -->
              <!-- <div class="form-group">
                <label for="imageUpload">上传图片:</label>
                <div class="upload-box">
                  <img :src="activeData.img" alt="预览" v-if="activeData.img" />
                  <div v-else class="add-img">
                    +
                  </div>
                  <input type="file" id="imageUpload" @change="handleImageChange" ref="fileInput" accept="image/*" />
                </div>
              </div> -->

              <!-- 文字输入 -->
              <div class="form-group">
                <label for="textInput">输入文字:</label>
                <input type="text" id="textInput" v-model="activeData.text" placeholder="请输入内容" />
              </div>
              <!-- 背景色 -->
              <div class="form-group">
                <label for="textInput">背景色:</label>
                <div v-for="option in options" :key="option.value" style="margin: 10px 0;">
                  <label :style="{ color: option.color }">
                    <input type="radio" v-model="activeData.bgColor" name="drone" :value="option.value" />
                    <div class="icon-bg" :style="{ background: option.value }"></div>
                  </label>


                </div>
              </div>

              <!-- 按钮 -->
              <div class="buttons">
                <button type="button" class="cancel-btn" @click="handleCancel()">取消</button>
                <button type="submit" class="confirm-btn" @click="handleSubmit()">确认</button>
              </div>
            </form>
          </div>
        </transition>
      </div>
    </transition>
    <audio ref="music" src="@/assets/music1.mp3" preload="auto"></audio>
    <audio ref="musicZj" src="@/assets/music2.mp3" preload="auto"></audio>
  </div>
</template>

<script>
// utils
import { px2vw } from '../utils'

// instance
export default {
  name: 'ninePrize',
  components: {},
  props: {
    // 奖盘宽度
    prizeWidth: {
      type: Number,
      default: 180
    },
    // 奖盘高度
    prizeHeight: {
      type: Number,
      default: 180
    },
    // 奖品集合
    // ds: {
    //   type: Array,
    //   default: () => []
    // },
    // 固定转几圈
    defaultCircle: {
      type: Number,
      default: 5
    },
    // 奖盘背景
    prizeBackground: {
      type: String,
      default: '#E5E5E5'
    },
    // 中心按钮背景
    pointerBackground: {
      type: String,
      default: '#EE2C2C'
    },
    // 奖品项背景
    prizeItemBackground: {
      type: String,
      default: '#fff'
    },
    // 中心按钮描述
    pointerText: {
      type: String,
      default: '开始抽奖'
    },
    // 中心按钮颜色
    pointerTextColor: {
      type: String,
      default: '#fff'
    },
    // 转动框颜色
    turnBackground: {
      type: String,
      default: '#333'
    }
  },
  data() {
    return {
      visible: false,
      editVisible: false,

      editType: ['编辑', '添加', '删除'],
      actveEditType: '',
      activeData: {
        imageFile: null,
        img: null,
        text: '',
        bgColor: ''
      },
      options: [
        { label: '蓝色', value: 'rgb(85 144 206 / 14%)', color: 'rgb(85 144 206 / 14%)' },
        { label: '白色', value: 'rgb(255 255 255 / 26%)', color: 'rgb(255 255 255 / 26%)' },
        { label: '粉色', value: 'rgb(244 162 162 / 29%)', color: 'rgb(244 162 162 / 29%)' },
      ],
      // imgList: [
      //   require('../assets/img01.jpg'),
      //   require('../assets/img02.jpg'),
      //   require('../assets/img03.jpg'),
      //   require('../assets/img04.jpg'),
      //   require('../assets/img05.jpg'),
      //   require('../assets/img06.jpg'),
      //   require('../assets/img07.jpg'),
      //   require('../assets/img08.jpg'),
      //   require('../assets/img09.jpg'),
      //   require('../assets/img10.jpg'),
      //   require('../assets/img11.jpg'),
      //   require('../assets/img12.jpg'),
      //   require('../assets/img13.jpg'),
      //   require('../assets/img14.jpg'),
      //   require('../assets/img15.jpg'),
      //   require('../assets/img16.jpg'),
      // ],
      ds: [
        { id: 1, isSelect: true, text: '梦夏1封+2米', img:  require('@/assets/img01.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 2, isSelect: false, text: '余额+32米', img:  require('@/assets/img02.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 3, isSelect: false, text: '心沐欢颜1封+6米', img:  require('@/assets/img03.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 4, isSelect: false, text: '花镜之约2封', img:  require('@/assets/img04.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },

        { id: 5, isSelect: false, text: '余额30米', img:  require('@/assets/img05.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 6, isSelect: false, text: '初夏1封+4米', img:  require('@/assets/img06.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 7, isSelect: false, text: '炽吻1封+4米', img:  require('@/assets/img07.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 8, isSelect: false, text: '繁星闪耀1封', img:  require('@/assets/img08.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },

        { id: 9, isSelect: false, text: '繁花与序章1封', img:  require('@/assets/img09.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 10, isSelect: false, text: '余额+35米', img:  require('@/assets/img10.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 11, isSelect: false, text: '转转任意选1个', img:  require('@/assets/img11.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 12, isSelect: false, text: '仲夏夜之梦1封+2米', img:  require('@/assets/img12.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 13, isSelect: false, text: '迷蝶梦1封', img: require( '@/assets/img13.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 14, isSelect: false, text: '后花园1封+2米', img: require( '@/assets/img14.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 15, isSelect: false, text: '定制A5卡册', img:  require('@/assets/img15.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
        { id: 16, isSelect: false, text: '烟雨1封', img:  require('@/assets/img16.jpg'), bgColor: 'rgb(255 255 255 / 26%)' },
      ],
      isStart: false, // 是否开启转动
      curRafID: null, // 当前rafid
      BIAN: 4, // 正方形有4条边
      DINGDIAN: 4, // 正方形有4个顶点
      tWidth: 0, // 每项宽度
      tHeight: 0, // 每项高度
      firstTurn: 0, // 第一次转弯处
      secondNeedNumber: 0, // 第二条边需再画几个
      abHeight: 0, // 渲染出的正方形固定高度
      abWidth: 0, // 渲染出的正方形固定宽度
      commonStyle: '', // 奖品项的公共样式
      curSelect: 0, // 转圈过程中当前奖品项
      spaceTimer: 0, // 初始间隔200ms
      lastSuccessTimer: Date.now(), // 上一次执行移动时间 ms
      countCircle: 0, // 圈数记录
      isFinalCircle: false, // 是否已执行完固定圈数
      bingoIndex: 0, // 中奖位置
      onOff: true,
      eachTimer: 17, // 到达指定圈数后最后转动连续基础加值
      finalStartTimer: 150, // 到达指定圈数后最后转动连续基础值
      leftTips: {

      },
      centeTips: {
        title: '赔本大转盘 启动!',
        text: '首单立减2米 仅需17.8 首次下单需要回复客服消息',
        remark: '未成年人禁止下单'

      }
    }
  },
  computed: {
    // 9宫格样式
    getPrizeStyle() {
      return {
        // width: '50%',
        // height: '50%'
        width: px2vw(this.prizeWidth),
        height: px2vw(this.prizeHeight),
        // backgroundColor: this.prizeBackground
      }
    },
    // 4 9分别起始间隔时间
    whichStartSpaceTimer() {
      return this.ds.length > 4 ? 34 : 150
    },
    // 指针宽高
    whichPointerWH() {
      // return this.ds.length === 8
      //   ? {
      //     width: px2vw(this.tWidth - 4),
      //     height: px2vw(this.tHeight - 4)
      //   }
      //   : {
      //     width: px2vw(40),
      //     height: px2vw(40)
      //   }
      return {
        width: '50%',
        height: '30vh'
      }
    }
  },
  mounted() {
    window.addEventListener('keydown', this.handleKeydown);
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.handleKeydown);
  },
  created() {
    // 初始化
    this.initAnimation()

    // 获取几次幂方阵
    this.firstTurn = (this.ds.length + this.DINGDIAN) / this.BIAN

    // 获取画第二条边 最多可画几个
    this.secondNeedNumber = this.firstTurn - 1

    // 计算出

    // 计算出每项宽高
    this.tWidth = this.prizeWidth / this.firstTurn
    this.tHeight = this.prizeHeight / this.firstTurn

    // 可得出边固定高或者宽
    this.abHeight = (this.firstTurn - 1) * this.tWidth
    this.abWidth = (this.firstTurn - 1) * this.tWidth

    // 公共宽高
    this.commonStyle = `width:${px2vw(this.tWidth)};height:${px2vw(
      this.tHeight
    )}`

    // 子元素公共宽高
    this.childCommonStyle = {
      width: px2vw(this.tWidth - 4),
      height: px2vw(this.tHeight - 3),
    }
    if (localStorage.getItem('centeTips')) {
      this.centeTips = JSON.parse(localStorage.getItem('centeTips'))
    }
    if (localStorage.getItem('dsInfo')) {
      this.ds = JSON.parse(localStorage.getItem('dsInfo'))
    }
    // 赋值初始间隔
    this.spaceTimer = this.whichStartSpaceTimer
  },
  destroyed() {
    this.curRafID && window.cancelAnimation(this.curRafID)
  },
  methods: {
    editDsList(type, data, index) {
      this.actveEditType = type;
      this.activeData = { ...data, index };
      if (type === 1) {
        this.activeData.img = '';
        this.activeData.text = '';
      }
      this.editVisible = true;
      console.log('data', data)
    },
    handleImageChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.imageFile = file;
        this.createImagePreview(file);
      } else {
        this.imageFile = null;
        this.activeData.img = null;
      }
    },
    createImagePreview(file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.activeData.img = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    handleSubmit() {
      if (!this.activeData.img || !this.activeData.text) {
        return false;
      }
      switch (this.actveEditType) {
        case 0:
          this.ds[this.activeData.index] = this.activeData;
          break;
        case 1:
          this.ds.splice(this.activeData.index + 1, 0, this.activeData);
          break
        case 2:
          this.ds.splice(this.activeData.index, 1)
          break
        default:
          break;
      }
      this.ds = [...this.ds]
      localStorage.setItem('dsInfo', JSON.stringify(this.ds))
      this.editVisible = false;
      // 这里可以处理提交逻辑，比如发送到后端
      // alert(`提交内容：文字=${this.text}, 图片=${this.imageFile ? this.imageFile.name : '无'}`);
    },
    handleCancel() {
      // 重置表单数据 
      // 如果需要清空文件输入
      this.$refs.fileInput.value = '';
      this.activeData = {
        imageFile: null,
        img: null,
        text: '',
      };
      this.editVisible = false;
    },
    hidePopup(refName) {
      this.visible = false;
      // 停止音乐
      this.$refs[refName].pause();
      // 可选：重置播放进度
      this.$refs[refName].currentTime = 0;
    },
    handleKeydown(e) {
      if (e.key === ' ' || e.key === 'Spacebar') {
        // 注意：在某些浏览器中，空格的 key 可能是 ' ' 或 'Spacebar'
        e.preventDefault(); // 避免滚动
        if (this.visible) {
          this.hidePopup('music')
        } else {
          console.log('isStart', this.isStart)
          if (!this.isStart) {
            this.onRun();
          }
        }
      }
    },
    getPositionClass(index, isSelect) {
      if (isSelect) {
        return 'active-'
      } else {
        return ''
      }
    },
    handleBlur(refName, objName, key) {
      const content = this.$refs[refName].innerHTML;
      this[objName][key] = content;
      localStorage.setItem(objName, JSON.stringify(this[objName]))
      // if(objName === 'centeTips'){
      //   this.centeTips[key] = content;
      //   console.log('content',content)
      //   localStorage.setItem('centeTips',this.centeTips)
      // }
    },
    // 获取位置
    getPosition(index, isSelect) {
      // 选中样式
      let selectStyle = ``
      if (isSelect) {
        const selectWidth = this.tWidth - 4
        const selectHeight = this.tHeight - 3
        selectStyle = `width:${px2vw(selectWidth)};height:${px2vw(selectHeight)};`
      }

      // 第一条边 index 0 1 2
      if (index < this.firstTurn) {
        let i = index - 0 * (this.firstTurn - 1)
        return `top:0;left:${px2vw(i * this.tWidth)};${this.commonStyle
          };${selectStyle}`
      }
      // 第二条边 index值从 3 4
      if (index - this.firstTurn < this.secondNeedNumber) {
        let i = index - 1 * (this.firstTurn - 1)
        return `top:${px2vw(i * this.tWidth)};left:${px2vw(this.abWidth)};${this.commonStyle
          };${selectStyle}`
      }
      // 第三条边 5 6
      if (index <= 3 * (this.firstTurn - 1)) {
        let i = index - 2 * (this.firstTurn - 1)
        return `top:${px2vw(this.abHeight)};left:${px2vw(
          this.abWidth - i * this.tWidth
        )};${this.commonStyle};${selectStyle}`
      }
      // 第四条边 7
      if (index < this.ds.length) {
        let i = index - 3 * (this.firstTurn - 1)
        return `top:${px2vw(this.abHeight - i * this.tWidth)};left:0;${this.commonStyle
          };${selectStyle}`
      }

    },
    initAnimation() {
      // 兼容性raf初始化
      window.requestAniFrame = (function () {
        return window.requestAnimationFrame

        // Older versions Chrome/Webkit
        window.webkitRequestAnimationFrame ||
          // Firefox < 23
          window.mozRequestAnimationFrame ||
          // opera
          window.oRequestAnimationFrame ||
          // ie
          window.msRequestAnimationFrame ||
          function (callback) {
            return window.setTimeout(callback, 1000 / 60)
          }
      })()

      // 兼容性取消初始化
      window.cancelAnimation = (function () {
        return (
          window.cancelAnimationFrame ||
          window.mozCancelAnimationFrame ||
          window.cancelRequestAnimationFrame ||
          function (id) {
            clearTimeout(id)
          }
        )
      })()
    },
    // run
    onRun() {
      this.$refs.music.play().catch(error => {
        console.log("音乐播放失败", error);
      });
      this.isStart = true
      this.bingoIndex = ~~(Math.random() * this.ds.length)
      this.onRunFunc()
    },
    // runfunc
    onRunFunc() {
      let rafId = window.requestAniFrame(this.onRunFunc)
      this.curRafID = rafId
      let nowTimer = Date.now()
      if (nowTimer - this.lastSuccessTimer >= this.spaceTimer) {
        this.onRender(() => {
          window.cancelAnimation(rafId)
          // 恢复时间间隔
          this.spaceTimer = this.whichStartSpaceTimer
          this.isFinalCircle = false
          this.onOff = true
          this.isStart = false
        })
      }
    },
    onRender(cb) {
      if (this.isFinalCircle) {
        // 固定圈数转完后 在1位置
        if (this.curSelect === this.bingoIndex && this.onOff) {
          this.visible = true;
          this.$refs.music.pause();
          // 可选：重置播放进度
          this.$refs.music.currentTime = 0;
          this.$refs.musicZj.currentTime = 0;
          this.$refs.musicZj.play().catch(error => {
            console.log('error音乐', error)
          });
          cb()
          return
        }
        // this.spaceTimer += this.eachTimer

        this.curSelect++
        if (this.curSelect < this.ds.length) {
          this.ds[this.curSelect]['isSelect'] = true
          this.ds[this.curSelect - 1]['isSelect'] = false
        } else if (this.curSelect === this.ds.length) {
          this.ds[this.curSelect - 1]['isSelect'] = false
          this.ds[0]['isSelect'] = true
        } else {
          // 转完一圈
          this.curSelect = 0
          this.onOff = true
        }
        this.$nextTick(() => {
          this.lastSuccessTimer = Date.now()
        })
      } else {
        this.curSelect++
        if (this.curSelect < this.ds.length) {
          this.ds[this.curSelect]['isSelect'] = true
          this.ds[this.curSelect - 1]['isSelect'] = false
        } else if (this.curSelect === this.ds.length) {
          this.ds[this.curSelect - 1]['isSelect'] = false
          this.ds[0]['isSelect'] = true
        } else {
          // 转完一圈
          this.curSelect = 1
          this.ds[0]['isSelect'] = false
          this.ds[1]['isSelect'] = true
          this.countCircle++
          if (this.countCircle === this.defaultCircle) {
            this.countCircle = 0
            // this.spaceTimer = this.finalStartTimer
            this.isFinalCircle = true
            if (this.bingoIndex === 1) {
              this.onOff = false
            }
          }
        }
        this.$nextTick(() => {
          this.lastSuccessTimer = Date.now()
        })
      }
    }
  }
}
</script>

<style lang='scss' scoped>
