  // item
  &-box {
    position: absolute;
    cursor: pointer;

    &:hover {
      background-color: rgba(0, 0, 0, 0.5)
    }

    // item
    &__item {
      @include fcc;
      position: absolute;

      .edit-box {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6); // 遮罩层颜色和透明度
        display: none; // 默认不显示
        color: #c6d3ff;
        font-size: 4px;
        // display: flex;
        // align-items: center;
        // flex-direction: column;
        // justify-content: center;
        padding-top: 8px;
        box-sizing: border-box;
      }

      &:hover .edit-box {
        display: inline-block; // 鼠标悬停时显示遮罩
      }

      // child
      .item-child {
        @include fcc(column);

        // border-radius: 10px;
        // img
        &-img {
          // width: 35px;
          // height: 35px;

          width: 100%;
          height: 100%;
          margin-top: 2px;

          &>img {
            width: 80%;
            // height: 100%;
            max-height: 25px;
          }
        }

        // text
        &-text {
