    handleImageChange(event) {
      const file = event.target.files[0];
      console.log('file',file)
      if (file) {
        this.bgImg = file;
        this.createImagePreview(file);
      } else {

        // this.bgImg = null;
        // this.activeData.img = null;
      }
    },
      createImagePreview(file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.bgImg = e.target.result;
        localStorage.setItem('bgImg',JSON.stringify(this.bgImg))
      };
      reader.readAsDataURL(file);
    },
    handleBlur(refName, objName, key) {
      const content = this.$refs[refName].innerHTML;
      this[objName][key] = content;
      console.log('oj',this.details)
      localStorage.setItem(objName, JSON.stringify(this.details))
